
import Box from 'vux/src/components/box/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Card from 'vux/src/components/card/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import AlertModule from 'vux/src/plugins/alert/module'
import Alert from 'vux/src/components/alert/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'


export default {

components: {
Box,
Group,
XButton,
Flexbox,
FlexboxItem,
Card,
XDialog,
XInput
},
data () {
var wallet = this.getWallet(this.$route.params.name);
if(!wallet) {
AlertModule.show('找不到钱包');
return false;
}
var coinName = this.$route.params.coinName;
console.log("获取coinname的值：", coinName);

return {
wallet:wallet,
coinName: coinName,
password:'',
amount:'',
gas:'',
address:''





}
},
methods: {
validate:function () {
if(!this.password) {
AlertModule.show('请输入密码')
return false
}
if(!this.amount) {
AlertModule.show('请输入金额')
return false
}
if(!this.address) {
AlertModule.show('请输入地址')
return false
}
if(!this.gas) {
AlertModule.show('请输入手续费')
return false
}

if( (parseFloat(this.gas)) < 21000) {
AlertModule.show('最少需要支付 21000 wei 作为手续费')
return false
}






return true
},
confirmTran:function() {
if(!this.validate()) {
return false
}


this.$vux.loading.show('转账中...');
var that = this;
setTimeout(function(){
that.sendCoinTran(that.coinName, that.wallet,that.address,that.amount,that.gas, that.password, null, function(err, hash){
that.$vux.loading.hide();
if(err) {
console.log('转账失败：', err)
if(err.message==='invalid address') {
AlertModule.show('账号不存在')
} else if (err.message==='base fee exceeds gas limit') {
AlertModule.show('手续费用超过限制')
} else {
AlertModule.show('转账错误：'+err.message)
}
return false;
}

AlertModule.show('转账已提交成功，交易hash：'+hash+'。')
});

},200);
}
}
}



