
import Box from 'vux/src/components/box/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'


export default {
components: {
Box,
Group,
Cell,
XButton,
Flexbox,
FlexboxItem,
XInput,
CheckIcon,
ButtonTab,
ButtonTabItem,
XTextarea
},
data() {
return {
importType: 0,
agree: false,
seed: '',
privateKey: '',
keystoreFile: '',
walletName: '',
walletPassword: '',
walletConfirmPassword: '',




msg: 'Hello World!'
}
},
methods: {
alert: function(msg) {
AlertModule.show({
content: msg
})
},
validate: function(importType) {
if (!this.walletName) {
this.alert('钱包名称不能为空');
return false;
}
if (this.getWallet(this.walletName)) {
this.alert('钱包已经存在，请更换名称');
return false;
}
if (!this.walletPassword) {
this.alert('钱包密码不能为空');
return false;
}
if (importType != 2) {
if (!this.walletConfirmPassword) {
this.alert('钱包确认密码不能为空');
return false;
}

if (this.walletPassword != this.walletConfirmPassword) {
this.alert('钱包两次密码不一致');
return false;
}
}

if (!this.agree) {
this.alert('请阅读并同意钱包协议');
return false;
}
return true;
},
importWallet: function(importType) {
var that = this;
if (!this.validate(importType)) {
return false;
}
this.$vux.loading.show({
text: '导入中..'
})

setTimeout(function() {
try {
if (importType === 0) {
that.importWalletFromSeed(that.walletName, that.seed, that.walletPassword);
} else if (importType === 1) {
that.importWalletFromPrivateKey(that.walletName, that.privateKey, that.walletPassword);
} else if (importType === 2) {
that.importWalletFromKeyStore(that.walletName, that.keystoreFile, that.walletPassword);
} else if (importType === 3) {
throw new Error("请选择合法的钱包导入方式");
}
} catch (e) {
console.log(e.message);
that.alert(e.message);
}
}, 200)

this.$vux.loading.hide()
},
}
}




